export function getAVG (list) {
  const sum = list.reduce((a, b) => a + b, 0)

  return sum / list.length
}

export function fixNumber (value, fixedLength) {
  // å°æ°è¡¥0é»è¾
  function fixed (fixNum = '', fixedLength) {
    // å¦ææå®ä¹è¦æ±è¡¥é¶çé¿åº¦
    if (fixedLength !== undefined) {
      if (fixedLength !== 0) {
        // å¦æä¸å­å¨å°æ°é¨å,å¨é¨è¡¥é¶
        if (!fixNum) {
          for (let index = 0; index < fixedLength; index++) {
            fixNum += '0'
          }
        } else if (fixNum.length > fixedLength) {
          // å¦æå°æ°é¨åçé¿åº¦æ¯è¦æ±è¡¥é¶çé¿åº¦é¿,æªå
          fixNum = fixNum.slice(0, fixedLength)
        } else {
          // å¦æå°æ°é¨åçé¿åº¦æ¯è¦æ±è¡¥é¶çé¿åº¦ç­,è¡¥é¶å©ä½é¨å
          for (
            let index = 0, l = fixedLength - fixNum.length;
            index < l;
            index++
          ) {
            fixNum += '0'
          }
        }
      } else if (fixedLength === 0) {
        // å¦æè¦æ±è¡¥é¶çé¿åº¦ä¸º0,çç¥å°æ°é¨å
        fixNum = ''
      }
    }

    if (fixNum) {
      fixNum = '.' + fixNum
    }

    return fixNum
  }

  if (value === 0) return '0'
  if (!value) return '--'
  let fixNum = value.toString().split('.')[1] // è·åå°æ°é¨å
  const intPart = Number(value.toString().split('.')[0]) // è·åæ´æ°é¨å
  fixNum = fixed(fixNum, fixedLength)

  if (
    value.toString().indexOf('-') !== -1 &&
    Math.abs(parseFloat(intPart)) <= 0
  ) {
    return '-' + `${intPart}${fixNum}`
  } else {
    return `${intPart}${fixNum}`
  }
}
